.container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));

  @media (min-width: 900px) {
    & {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  &__item {
    display: grid;
    height: $item-size;
    justify-content: center;
    align-content: center;

    color: white;
  }
}
